export default {
	data() {
		return {
			years: [],
			// yearsIndex:0,
			yearss: [],
			city_one: '',
			city_num: '',
			city_first: false,
			city_two: '',
			city_nums: '',
			city_second: false,
			maozhong: '',
			tiji: '',
			name: '',
			phone: '',
			local: '',
			inter: '',
			baseUrl: '',
			isCheck: 0,
			isChecks: 0,
			mark:false,
			hide:false,
			lianxiren:"",
			lianxirentel:""
		};
	},
	onLoad() {
		uni.request({
			url: 'http://td.gfapki.com.cn:8090/country-indices',
			method: 'GET',
			// data: {
			// },
			success: res => {
				console.log(eval(res.data));
				// this.years = res.data;
				// this.yearss = res.data;
				var arr = res.data;
				var arr1 = [];
				var arr2 = [];
				var arr3 = [];
				for (var key in arr) {
					arr1.push(arr[key]);
				}
				arr1.sort();
				for (var i = 0; i < arr1.length; i++) {
					for (var key in arr) {
						if (arr1[i] == arr[key]) {
							console.log('key' + key);
				
							arr2.push({ key: key, value: arr1[i] });
							arr3.push({ key: key, value: arr1[i] });
							break;
						}
					}
				}
				console.log(arr2);
				this.years = arr2;
				this.yearss = arr3;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	onShow() {
		uni.setNavigationBarTitle({
			title: this.$t('index.navTitleTwo')
		})
		uni.setTabBarItem({
			index:0,
		    text: this.i18n.navTitle
		});
		uni.setTabBarItem({
			index:2,
		    text: this.i18n.navTitleTwo
		});
		uni.setTabBarItem({
			index:1,
		    text: this.i18n.navTitleThree
		});
		uni.setTabBarItem({
			index:3,
		    text: this.i18n.navTitleFour
		});
		// console.log('now:' + this.websiteUrl);
		this.baseUrl = this.websiteUrl;
		let token = uni.getStorageSync('storage_token');
		// console.log(this.baseUrl);
			// 联系人方式
			uni.request({
				url: this.baseUrl + "/api/getContacts",
				method: 'GET',
				// data: {
				// },
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					console.log(res.data);
					this.lianxiren = res.data.data.mobile;
					this.lianxirentel = res.data.data.person_in_charge;
				},
				fail: () => {},
				complete: () => {}
			});
	},
	computed: {
		i18n() {
			return this.$t('index');
		}
	},
	methods: {
		sign() {
			uni.navigateTo({
				url: '../teggs/teggs'
			});
		},
		chooseClick(num, index) {
			console.log(index.value);
			console.log(index.key);
			console.log(num);
			this.city_one = index.value;
			this.city_num = index.key;
			this.isChecks = index;
		},
		first() {
			this.city_first = true;
		},
		cancelFirst() {
			this.city_first = false;
			// let as = this.city_one;
			// this.city_one = "请选择";
		},
		sureFirst() {
			this.city_first = false;
			this.city_one = this.city_one;
		},
		chooseClicks(num, index) {
			console.log(index.value);
			console.log(index.key);
			this.city_two = index.value;
			this.city_nums = index.key;
			this.isCheck = index;
		},
		second() {
			this.city_second = true;
		},
		cancelSecond() {
			this.city_second = false;
			// this.city_two = '请选择';
		},
		sureSecond() {
			this.city_second = false;
			this.city_two = this.city_two;
		},
		messagesearch: function(event) {
			this.maozhong = event.target.value;
		},
		tijisearch: function(event) {
			this.tiji = event.target.value;
		},
		namesearch: function(event) {
			this.name = event.target.value;
		},
		phonesearch: function(event) {
			this.phone = event.target.value;
		},
		localsearch: function(event) {
			this.local = event.target.value;
		},
		intersearch: function(event) {
			this.inter = event.target.value;
		},
		submit() {
		
			console.log('第一个国家名' + this.city_one);
			console.log('第一个国家索引' + this.city_num);
			console.log('第二个国家名' + this.city_two);
			console.log('第二个国家索引' + this.city_nums);
			console.log('毛重' + this.maozhong);
			console.log('体积' + this.tiji);
			console.log('姓名' + this.name);
			console.log('手机号' + this.phone);
			console.log('出发地' + this.local);
			console.log('目的地' + this.inter);
			let token = uni.getStorageSync('storage_token');
			let city_one = this.city_one;
			
			var that = this;
			
			console.log(token);
			if(token == "") {
				uni.showToast({
					title: '请登录后提交',
					icon: 'none',
					duration: 800
				});
				return false;
			}
			if (city_one == '') {
				uni.showToast({
					title: '请选择出发地',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (this.local == '' || this.local == null || this.local == undefined) {
				uni.showToast({
					title: '请输入港口/机场',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (this.city_two == '') {
				uni.showToast({
					title: '请选择目的地',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (this.inter == '' || this.inter == null || this.inter == undefined) {
				uni.showToast({
					title: '请输入港口/机场',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (this.maozhong == '' || this.maozhong == null || this.maozhong == undefined) {
				uni.showToast({
					title: '请输入毛重',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (this.tiji == '' || this.tiji == null || this.tiji == undefined) {
				uni.showToast({
					title: '请输入体积',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (this.name == '' || this.name == null || this.name == undefined) {
				uni.showToast({
					title: '请输入姓名',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (this.phone == '' || this.phone == null || this.phone == undefined) {
				uni.showToast({
					title: '请输入电话',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (!/^1[3456789]\d{9}$/.test(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					duration: 800
				});
				return false;
			}
			uni.request({
				url: this.baseUrl + '/api/yitihua',
				method: 'post',
				data: {
					country_name1: this.city_one,
					country_index1: this.city_num,
					airport1: this.local,
					country_name2: this.city_two,
					country_index2: this.city_nums,
					airport2: this.inter,
					weight: this.maozhong,
					volume: this.tiji,
					realname: this.name,
					phone: this.phone
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					console.log(res.data);
					console.log(token);
					if (res.data.code == '200') {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 500
						});
						this.local = '';
						this.inter = '';
						this.maozhong = '';
						this.tiji = '';
						this.name = '';
						this.phone = '';
						
						setTimeout(function(){
							that.mark = true;
							that.hide = true;
							// uni.showModal({
							//     title: '提示',
							//     content: '会员折扣9.8折会员折扣9.8折会员折扣9.8折会员折扣9.8折会员折扣9.8折',
							//     success: function (res) {
							//         if (res.confirm) {
							//             console.log('用户点击确定');
							// 			that.mark = true;
							// 			that.hide = true;
							//         } else if (res.cancel) {
							//             console.log('用户点击取消');
							//         }
							//     }
							// });
						},400)
							
					} else if (res.data.code == '203') {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 800
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		markBtn() {
			this.mark = false;
			this.hide = false;
		},
		back() {
			uni.switchTab({
				url: '../index/index'
			});
		}
	}
};
